/**
 * Save new video watched states.
 *
 * @param {Object} video - intent, context and movie data
 * @param {boolean} end - end of file
 */
function saveVideo ( video, end ) {
    var intent = video.intent,
        context = video.context,
        item, position, duration,
        progress, config;

    // check save function
    function add ( error, data ) {
        var statusText, disabled;

        if ( !error && data && data.status ) {
            item.file['watched_status'] = config['watched_status'];

            if ( item.season ) {
                item.movie['last_watched_season_id'] = item.season.id;
                item.season['watched_status'] = data['season_watched_status'];
            }

            if ( item.episode ) {
                switch ( config['watched_status'] ) {
                    case 'watched':
                        statusText = gettext('Watched');
                        disabled = true;
                        break;
                    case 'not_watched':
                        statusText = '';
                        disabled = false;
                        break;
                    case 'not_ended':
                        statusText = gettext('Not finished');
                        disabled = false;
                        break;
                }
                item.episode['watched_status'] = config['watched_status'];
                item.movie['last_watched_episode_id'] = item.episode.id;

                if ( item.episode.status ) {
                    item.episode.status.$focus.innerText = statusText;
                    item.episode.status.$blur.innerText = statusText;
                    if ( disabled ) {
                        item.episode.status.$focus.classList.add('disabled');
                        item.episode.status.$blur.classList.add('disabled');
                    } else {
                        item.episode.status.$focus.classList.remove('disabled');
                        item.episode.status.$blur.classList.remove('disabled');
                    }
                }
            }

            app.pages.main.emit('change:watched', infoData);

            if ( infoData['watched_status'] === 'not_ended' ) {
                if ( infoData['is_tv_series'] ) {
                    buttonList.setData({data: buttonsSerialsWatch, focusIndex: 0});
                } else {
                    buttonList.setData({data: buttonsDataWatch, focusIndex: 0});
                }
            } else {
                buttonList.setData({data: buttonsData, focusIndex: 0});
            }
        }

    }

    if ( intent.data && context ) {
        position = context.metadata.position;
        duration = context.metadata.duration;
        // position = intent.playback.position;
        // duration = intent.playback.duration;
        item = intent.data;
        if ( position > 10 ) {
            if ( position && duration ) {
                progress = position / duration * 100;
                if ( progress > 95 || end ) {
                    config = {
                        watched_status: 'watched',
                        watched_time: position - 10
                    };
                } else if ( progress > 5 ) {
                    config = {
                        watched_status: 'not_ended',
                        watched_time: position - 10
                    };
                }
                config.progress = progress;
                item.file['watched_status'] = config['watched_status'];
                item.file['watched_time'] = config['watched_time'];
                item.movie['watched_time'] = config['watched_time'];
                item.movie['last_watched_file_id'] = item.file.id;
                item.movie.changeWatchedFile(item.file.id, config, add);
            }
        }
    }
}

/**
 * Prepare object to play.
 *
 * @param {Object} data - data to play
 * @param {number} position - start position
 * @param {Object} context - player context
 */
function prepareToPlay ( data, position, context ) {
    var item = data,
        name = infoData.name,
        // episode = null,
        intent = null,
        next = null,
        prev = null;

    // if ( infoData.is_tv_series ) {
    //     episode = seriesList.$focusItem.data;
    // }

    function selectNextFile ( file, languages ) {
        var files = utils.sortQuality(file.files, infoData);

        files = utils.prepareFiles(files.video);
        file = utils.selectFile(files, languages, intent.data.file);

        if ( file ) {
            prepareToPlay(file.value, 0, context);
        } else {
            fillFileList(seriesList.$focusItem.data.value, function () {
                seriesList.blur();
                selectFileModal.prevFocus = seriesList;
                setTimeout(function () {
                    selectFileModal.show();
                }, app.config.delay.modalShow);
            });

            context = null;
            intent.close();
        }
    }

    /**
     * Play object.
     *
     * @param {boolean} error - error
     * @param {string} uri - uri to play
     */
    function play ( error, uri ) {
        var index = 0;

        if ( error ) {

            return;
        }

        if ( infoData.is_tv_series ) {
            name += ' ' + season.custName + ' ' + episode.custName;
            index = episodes.indexOf(episode);

            if ( index + 1 < episodes.length ) {
                next = function () {
                    var lang = context.metadata.audioLang;

                    saveVideo({intent: intent, context: context});
                    index = episodes.indexOf(episode);

                    if ( index === -1 ) {
                        context = null;
                        intent.close();

                        return;
                    }

                    if ( seriesList.visible ) {
                        seriesList.move(keys.down);
                        episode = seriesList.$focusItem.value;
                    } else {
                        episode = episodes[++index];
                    }
                    selectNextFile(episode, lang);
                };
            }

            if ( index > 0 ) {
                prev = function () {
                    var lang = context.metadata.audioLang;

                    saveVideo({intent: intent, context: context});
                    index = episodes.indexOf(episode);

                    if ( index === -1 ) {
                        context = null;
                        intent.close();

                        return;
                    }

                    if ( seriesList.visible ) {
                        seriesList.move(keys.up);
                        episode = seriesList.$focusItem.value;
                    } else {
                        episode = episodes[--index];
                    }
                    selectNextFile(episode, lang);
                };
            }
        }

        function startPlay () {
            intent = core.intent({
                action: 'play',
                mime: 'content/video',
                data: {
                    title: name,
                    uri: uri,
                    movie: infoData,
                    file: item,
                    season: season,
                    episode: episode,
                    position: position,
                    subtitles: configFiles.subtitles,
                    mediaId: item.id
                },
                events: {
                    end: function () {
                        if ( next ) {
                            next();

                            return;
                        }
                        saveVideo({intent: intent, context: context}, true);
                        context = null;
                        intent.close();
                    },
                    error: function () {
                        saveVideo({intent: intent, context: context});
                        context = null;
                        intent.close();
                    },
                    stop: function () {
                        saveVideo({intent: intent, context: context});
                        context = null;
                        intent.close();
                    },
                    hide: function () {
                        saveVideo({intent: intent, context: context});
                        context = null;
                        intent.close();
                    },
                    next: next,
                    prev: prev,
                    close: function () {
                        if ( context ) {
                            saveVideo({intent: intent, context: context});
                            context = null;
                        }
                    }
                },
                context: context
            }, function ( error, cont ) {
                debug.fail('Play error', error);
                context = cont;
            });
        }

        startPlay();
    }

    if ( item.url ) {
        play(false, item.url);
    } else {
        infoData.getUrl(item.id, play);
    }
}

/**
 * Get, prepare and set files data.
 *
 * @param {Object} config - to request
 * @param {Function} callback - result handler
 */
fillFileList = function ( config, callback ) {
    var list = [];

    if ( infoData.is_tv_series ) {
        configRequest.episodeId = config.id;
        episode = config;
        if ( config.files && config.files.length ) {
            configFiles = utils.sortQuality(config.files, infoData);
            list = utils.prepareFiles(configFiles.video);
            if ( list.length > 0 ) {
                if ( list.length > 1 ) {
                    fileList.setData({focusIndex: 0, data: list});
                } else {
                    prepareToPlay(list[0].value);

                    return;
                }
            }

            callback();
        } else {
            infoData.getEpisode(configRequest, function ( error, data ) {
                if ( error ) {
                    configFiles = {video: [], subtitles: []};
                } else {
                    configFiles = utils.sortQuality(data.files, infoData);
                    list = utils.prepareFiles(configFiles.video);
                }

                if ( list.length > 0 ) {
                    if ( list.length > 1 ) {
                        fileList.setData({focusIndex: 0, data: list});
                    } else {
                        prepareToPlay(list[0].value);

                        return;
                    }
                }

                callback();
            });
        }
    } else {
        episode = null;
        season = null;
        configFiles = utils.sortQuality(infoData.files, infoData);
        list = utils.prepareFiles(configFiles.video);
        if ( list.length > 0 ) {
            if ( list.length > 1 ) {
                fileList.setData({focusIndex: 0, data: list});
            } else {
                prepareToPlay(list[0].value);

                return;
            }
        }

        callback();
    }
};

/**
 * Prepare and set season data.
 *
 * @param {Object} data - history
 */
function fillSeason ( data ) {
    data = data || {};
    seriesList.title.innerText = gettext('Seasons');
    if ( seasonData.length ) {
        seriesList.setData({focusIndex: data.position || 0, data: utils.prepareSeasons(seasonData, true)});
    } else {
        seriesList.setData({focusIndex: 0, data: []});
    }
}

/**
 * Get, prepare and set series data.
 *
 * @param {Object} config - to request
 * @param {Function} callback - result handler
 */
function fillSeries ( config, callback ) {
    season = config;
    configRequest.seasonId = config.id;
    seriesList.title.innerText = config.custName;

    infoData.getSeries(config.id, function ( error, data ) {
        var list = error ? [] : utils.prepareSeries(data);

        episodes = data;
        seriesList.setData({focusIndex: 0, data: list});
        callback();
    });
}

/**
 * Change favorite state.
 */
function toggleFavorite () {
    if ( infoData.favorite ) {
        buttonList.$node.classList.remove('inFavorite');
    } else {
        buttonList.$node.classList.add('inFavorite');
    }
    utils.toggleFavorite(infoData);
}


function prepareFiles () {
    if ( infoData.is_tv_series ) {
        if ( seasonData.length ) {
            season = seasonData[0];
            if ( !season.custName ) {
                season.custName = season.name ? season.name +
                    ( season.original_name ? ' ( ' + season.original_name + ' )' : '' ) : gettext('Season') + ' ' + season.number;
            }
            infoData.getSeries(season.id, function ( error, data ) {
                if ( !error && data.length ) {
                    utils.prepareSeries(data);
                    episodes = data;
                    episode = data[0];

                    fillFileList(episode, function () {
                        buttonList.blur();
                        selectFileModal.prevFocus = buttonList;
                        setTimeout(function () {
                            selectFileModal.show();
                        }, app.config.delay.modalShow);

                    });

                }
            });
        }
    } else {
        fillFileList(null, function () {
            buttonList.blur();
            selectFileModal.prevFocus = buttonList;
            setTimeout(function () {
                selectFileModal.show();
            }, app.config.delay.modalShow);
        });
    }
}


function startLastWatched () {
    var indexes = {},
        file, index;

    function checkFile ( data ) {
        var list;

        configFiles = utils.sortQuality(data, infoData);
        list = configFiles.video;
        if ( !list.length ) {
            return;
        }

        if ( infoData.last_watched_file_id ) {
            for ( index = 0; index < list.length; index++ ) {
                if ( list[index].id === infoData.last_watched_file_id ) {
                    file = list[index];
                    break;
                }
            }
        } else {
            file = list[0];
        }
        prepareToPlay(file, infoData.watched_time);
    }

    if ( infoData.is_tv_series ) {
        if ( infoData.last_watched_season_id ) {
            for ( index = 0; index < seasonData.length; index++ ) {
                if ( seasonData[index].id === infoData.last_watched_season_id ) {
                    season = seasonData[index];
                    if ( !season.custName ) {
                        season.custName = season.name ? season.name +
                            (season.original_name ? ' ( ' + season.original_name + ' )' : '') : gettext('Season') + ' ' + season.number;
                    }
                    indexes.season = index;
                    break;
                }
            }

            infoData.getSeries(season.id, function ( error, data ) {
                if ( !error && data.length ) {
                    utils.prepareSeries(data);
                    episodes = data;
                    if ( infoData.last_watched_episode_id ) {
                        for ( index = 0; index < data.length; index++ ) {
                            if ( data[index].id === infoData.last_watched_episode_id ) {
                                episode = data[index];
                                episode.custName = episode.name ? episode.name +
                                    (episode['original_name']
                                        ? ' ( ' + episode['original_name'] + ' )'
                                        : '') : gettext('Series') + ' ' + episode.number;
                                break;
                            }
                        }
                    }

                    if ( episode.files && episode.files.length ) {
                        checkFile(episode.files);
                    } else {
                        infoData.getEpisode({seasonId: season.id, episodeId: episode.id}, function ( error, episodeData ) {
                            if ( !error ) {
                                checkFile(episodeData.files);
                            }
                        });
                    }

                }
            });
        }
    } else {
        checkFile(infoData.files);
    }
}

