buttonList = new LayoutList({
    cycle: false,
    className: 'buttonList',
    render: function ( $item, config ) {
        var layout, focus, icon,
            button;

        while ( $item.firstChild ) {
            $item.removeChild($item.firstChild);
        }
        button = tag('div', {className: 'button'});
        icon = tag('div', {className: config.icon});
        button.appendChild(icon);

        focus = tag('div', {className: 'buttonFocus'});
        icon = tag('div', {className: config.icon});
        focus.appendChild(icon);

        while ( $item.firstChild ) {
            $item.removeChild($item.firstChild);
        }

        if ( config.value.length === 1 ) {
            layout = new Layout({
                focusable: false,
                data: [
                    button,
                    {className: 'buttonTitle', value: config.value[0]},
                    focus
                ]
            });
        } else {
            layout = new Layout({
                focusable: false,
                data: [
                    button,
                    {className: 'buttonFirstTitle', value: config.value[0]},
                    {className: 'buttonSecondTitle', value: config.value[1]},
                    focus
                ]
            });
        }

        $item.appendChild(layout.$node);
        $item.layout = layout;
        layout.parent = this;
        layout.$parentItem = $item;

        // focus layoutList if click on layout
        layout.addListener('click', function () {
            // add inner property to set that event comes from inner component
            layout.parent.emit('click:item', {$item: $item, inner: true});
        });

        if ( config.click ) {
            this.handlers[$item.index] = config.click;
        }
        // item is rendered
        $item.ready = true;

        $item.value = config.value || {};
    },
    data: [],
    size: app.metrics.buttonList.size,
    focusIndex: 0,
    type: LayoutList.prototype.TYPE_HORIZONTAL,
    events: {
        keydown: function ( event ) {
            switch ( event.code ) {
                case keys.up:
                    event.stop = true;
                    if ( buttonMore.visible ) {
                        buttonMore.focus();
                    }
                    break;
                case keys.down:
                    event.stop = true;
                    if ( screenList.visible ) {
                        screenList.focus();
                        page.$node.scrollTop = app.metrics.infoSroll.screen;
                    } else if ( seasonsButton.visible ) {
                        seasonsButton.focus();
                        page.$node.scrollTop = app.metrics.infoSroll.season - app.metrics.infoSroll.screen;
                    } else if ( seasonsList.visible ) {
                        seasonsList.focus();
                        page.$node.scrollTop = app.metrics.infoSroll.season - app.metrics.infoSroll.screen;
                    }
                    break;
                case keys.right:
                case keys.left:
                    // cursor move only on arrow keys
                    this.move(event.code);
                    event.stop = true;
                    break;
                case keys.enter:
                    // there are some listeners
                    if ( this.events['click:item'] && this.$focusItem ) {
                        // notify listeners
                        this.emit('click:item', {$item: this.$focusItem, event: event});
                    }
                    event.stop = true;
                    break;
            }
        }
    }
});

